<template>
    <div class="IndexedDB contents">
        <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
        <mt-field label="查询类型" :placeholder='"请输入"+selectType+"(默认查询全部)"' v-model="sortVal"></mt-field>
        <mt-button class="btn" type="primary" @click="doQuery">查询</mt-button>
    </div>
</template>

<script>
    import DB from '../../../assets/js/IndexedDB'
    export default{
        name: 'IndexedDB',
        data(){
            return{
                list: null,
                selectType: ['姓名'],
                sortVal: '',
                slots: [{
                    flex: 1,
                    values: [ '姓名', '手机号', '卡号'],
                    className: 'slot1'
                }]
            }
        },
        methods:{
            onValuesChange(picker, values) {
                this.selectType = values;
		    },
            doQuery() { 
                this.$bus.emit('user-list', this.list);
                this.$router.push({path:this.$route.fullPath + '/do',query:{index: this.selectType[0], value: this.sortVal}});
                this.$bus.emit('select-title', '操作结果');
                //this.$bus.emit('child-page', true);

            },
            doRead(name) {
                return new Promise( (resolve, reject) => {
                    DB.read_data(name).then( (val) => {
                        this.list = val;
                        resolve('数据读取成功');
                        reject('数据读取成功');
                    });
                });
            }
        }
    }
</script>



