<template>
	<div v-cloak>
		<!--	底部导航栏	-->
		<tab-bar :select="activeTab"></tab-bar>
		
		<!--	顶部导航栏	-->
		<nav-bar :title="activeTitle" :children="child" :titleMap="titleMap"></nav-bar>
		
		<!--	弹出背景层	-->
		<div class="bg-cover" v-if="search"></div>
		
		<!--	弹出搜索框	-->
		<search  v-if="search"></search>

		<!--	登录框	-->
		<login v-if="noSign"></login>
		
		
		<div class="container">
			<router-view ></router-view>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
	import tabBar from './components/tabBar';
	import navBar from './components/navBar';
	import search from './components/search';
	import login from './components/login';
	import JsonData from './components/common/json'; 
	export default {
		name: 'app',
		components: {
			tabBar,
			navBar,
			search,
			login
		},
		data() {
			return {
				search: false,
				noSign: true
			}
		},
		computed: {
			'preHash': function() {
				//获取url的hash值
				let hash = this.$route.fullPath.substring(1).split('?')[0];
				return hash;
			},
			'titleMap': function() {
				return JsonData.titleMap;
			},
			'child': function() {
				/*
				 * 初始化页面的时候判断是否是进入了子页面（主要针对pc端，app不存在进入首页以外的页面）
				 */
				let len = this.preHash.split('/').length;
				return len == 1 ? false : true;
			},
			'activeTitle': function() {
				//根据hash值判断当前激活路由的顶部title
				let self = this;
				let map = JsonData.menu.filter( (item) => {
					return item.hash == self.preHash;
				});
				return map[0].name;
				/*let tab = this.titleMap[this.preHash];
				return tab;*/
			},
			'activeTab': function() {
				//根据hash值判断当前激活路由的底部tab
				let val = this.preHash.split('/')[0];
				let tab = this.titleMap[val];
				return tab;
			},
		},
		methods: {
			showSearch: function(val) {
				this.search = val;
			},
			hideSign(val) {
				this.noSign = val;
			}
		},
		created() {
			let minutes, staus;
			this.$bus.on('search', this.showSearch);
			this.$bus.on('hide-sign', this.hideSign);
			
			let timeNow = new Date().getTime();
			let timeLast = sessionStorage.getItem('loginStaus') || null;
			if(timeLast == null && this.homePath != "") {
				alert('您还未登录！');
				this.noSign = true;
			} else {
				staus = timeNow - timeLast;
				//计算出相差天数
				let days = Math.floor(staus / (24 * 3600 * 1000))
				//计算出小时数
				let leave1 = staus % (24 * 3600 * 1000) //计算天数后剩余的毫秒数
				let hours = Math.floor(leave1 / (3600 * 1000))
				//计算相差分钟数
				let leave2 = leave1 % (3600 * 1000)
				//计算小时数后剩余的毫秒数
				minutes = Math.floor(leave2 / (60 * 1000))
			
				if(minutes < 10) {
					this.noSign = false;
				}else {
					alert('登录有效超时！');
				}
			}
			
		}
	};
</script>
<style scoped>
	
	
</style>


