<template>
	<div class="tabBar">
		<mt-tabbar class="tab-bar" v-model="selfSelect">
			<mt-tab-item id="首页" @click.native="changeTab('home')">
				<i class="iconfont icon-home-tab"></i>
				<div>首页</div>
			</mt-tab-item>
			<template v-for="item in tabList">
				<mt-tab-item :id="item.name" @click.native="changeTab(item.hash)">
					<i :class="item.cl" class="iconfont"></i>
					<div>{{item.name}}</div>
				</mt-tab-item>
			</template>
			<mt-tab-item id="常用" @click.native="changeTab('offen')">
				<i class="iconfont icon-cy"></i>
				<div>常用</div>
			</mt-tab-item>
		</mt-tabbar>
	</div>
</template>
<script>
	import JsonData from './common/json'
	export default {
		name: 'tabBar',
		props: {
			select: String
		},
		data() {
			return {
				selfSelect: this.select,
				tabList: [
				{
		            "hash": "home/account/add",
		            "cl": "icon-xinzengyonghu",
		            "index": "1-1-0",
		            "name": "存入账户"
		        }, {
		            "hash": "home/account/query",
		            "cl": "icon-kehuxinxichaxun",
		            "index": "1-1-0",
		            "name": "查询账户"
		        }]
			}
		},
		methods: {
			changeTab: function(tab) {
				this.$router.replace('/' + tab); /* 使用replace替代push，好处是不产生历史 */
				this.$bus.emit('select-title', this.selfSelect);
				//抛出当前选择的tab，更改title值和tab激活
				this.$bus.emit('to-parent', false);
				//点击tab抛出此时激活的是tab主页面，不需要返回键
			},
			activeTab: function(val) {
				this.selfSelect = val;
			},
			setTab: function(val) {
				let data = JsonData.menu;
				let obj1 = data.filter(function(item) {
					return item.hash == val[0];
				});
				let obj2 = data.filter(function(item) {
					return item.hash == val[1];
				});
				this.tabList = obj1.concat(obj2); 
				
			}
		},
		created: function() {
			this.$bus.on('select-tab', this.activeTab);//获取来自搜索框激活的tab
			this.$bus.on('set-tab', this.setTab);
		}
	}
</script>

<style scoped>
	.tabBar {
		z-index: 99;
		position: fixed;
		width: 100%;
		bottom: 0px;
	}
	.tab-bar {
	    position: absolute;
	    bottom: 0px;
	}
</style>


