<template>
    <div class="IndexedDB">
        <template v-if="has">
            <div class="user-content" v-for="(value, index) in usersList">
                <!--    操作该条信息  -->
                <img src='../../../assets/img/more.png' class="icon-del" @click.stop="doMore(value)"/>
                <domore-dialog :list="doMoreList" v-if="more == value.call"></domore-dialog>
                <!--    操作该条信息  -->
               
                <!--    查询结果：用户列表   -->
                <ul ref="refList">
                    <li class="user-list" v-for="(value, key) in value">{{labelMap[key]}}：{{value}}</li>
                </ul>
            </div>
            <div class="page-btn">
                <mt-button type="primary" :disabled="!goLast" @click="last">上一页</mt-button>
                <mt-button type="primary" :disabled="!goNext" @click="next">下一页</mt-button>
            </div>
            
            <!--    编辑该条信息    -->
            <template v-if="edit">
                <div class="user-edit-dialog">
                    <div class="user-edit-content">
                        <edit-list :uesrInfo="editInfo" class="edit-temp"></edit-list>
                    </div>
                    <i class="iconfont icon-iconfontshibai icon-close" @click.stop="edit = false"></i>
                </div>
            </template>
        </template>
        <!--    没有信息显示内容    -->
        <template v-if="!has">
            <p class="no-data-title">啊哦！你的数据被偷走了，立刻去存点数据吧</p>
            <i class="iconfont icon-shibai no-data"></i>
        </template>
    </div>
</template>

<script>
    import DB from '../../../assets/js/IndexedDB'       //引入IndexedDB，数据库操作文件
    import Page from '../../../assets/js/page'
    import editList from '../../common/editList'        //修改用户弹出层
    import domoreDialog from '../../common/domoreDialog'    //更多选择弹出层
    import {
		Indicator,
		Toast,
		MessageBox
	} from 'mint-ui';
    export default{
        name: 'IndexedDB',
        components: {
            editList,
            domoreDialog
        },
        data(){
            return{
                usersList: null,
                has: true,
                edit: false,
                more: null,
                page: 1,
                goNext: false,  //是否显示下一页
                goLast: false,  //是否显示上一页
                doMoreList: ['编辑', '删除', '收藏'],
                map: {
                    '姓名': 'name',
                    '手机号': 'call',
                    '卡号': 'card'
                },
                //根据index，回显汉字title
                labelMap: {
					name: '姓名',
                    call: '手机',
					card: '卡号',
                    money: '余额',
					date: '日期',
					info: '备注',
					sign: '标注用户'
				},
				editInfo: null  //当前被操作的信息
            }
        },
        computed: {
            /*
             *  获取父级页面查询类型和查询值
             */
            index() {
                return this.map[this.$route.query.index];
            },
            value() {
                return this.$route.query.value;
            }
        },
        methods: {
            //显示更多操作
            doMore(item) {
                this.more = item.call;
                this.editInfo = item;
            },
            //点击更多操作，执行相应方法
            doIt(val) {
                this.more = null;  
                if(val == '编辑') {
                    this.doEdit();
                }
                else if(val == '删除') {
                    this.delUser(this.editInfo);
                }
                else {
                    let data = this.editInfo;
                    this.doSign(data);
                }
                
                         
            },
            //编辑用户
            doEdit() {
                this.edit = true;
            },
            //删除用户
            delUser(item) {
                let self = this;
                MessageBox({
					title: '提示',
					message: '确定删除该条信息?',
					showCancelButton: true
				}).then( action => {
					if(action == 'confirm') {
                        DB.del_data('customer', item.card).then( (val) => {
                            let instance = Toast({
                                message: val,
                                duration: 500,
                                iconClass: 'mintui mintui-success'
                            });
                            self.doReload();
					    });
					}
				});

            },
            doSign(data) {
                let self = this;
                MessageBox({
                    title: '提示',
                    message: '确定收藏该条信息?',
                    showCancelButton: true
                }).then( action => {
                    if(action == 'confirm') {
                        data.sign = '加挂帐户';
                        self.doUpdate('customer', data).then( () => {
                            self.doReload();
                        });
                    }
                });
            },
            doUpdate(name, obj) {
                return new Promise(function(resolve, reject) {
                    DB.update_data(name, obj).then( (val) => {
                        resolve(val);
                    }, function(reject) {
                        alert(reject);
                    });
                });
            },
            doHide(val) {   //隐藏编辑操作弹出层
                this.edit = val;
                this.doReload();
                
            },
            hideMe(val) { //隐藏更多操作弹出层
                this.more = val;
            },
            doReload() {    //刷新数据
                DB.query_data('customer', this.index, this.value).then( (val) => {
                    this.usersList = val;
                    this.has = this.usersList.length == 0 ? false : true;
                });
            },
            last() {
                Page.last('customer', this.page).then( (response) => {
                    this.page = Number(this.page) - 1
                    this.goNext = true;
                    this.usersList = response.data;
                    this.goLast = response.last;
                })
            },
            next() {
                Page.next('customer', this.page).then( (response) => {
                    this.page = Number(this.page) + 1;
                    this.goLast = true;
                    this.usersList = response.data;
                    this.goNext = response.next;
                })
            }
        },
        created() {
            this.$bus.on('hide-edit-dialog', this.doHide);  //隐藏编辑操作弹出层
            this.$bus.on('hide-doMore', this.hideMe);       //隐藏更多操作弹出层
            this.$bus.on('todo-some', this.doIt);  //显示具体操作弹出层
            //打开数据库并查询信息
           /* DB.open('customer').then( (resolve) => {
                DB.query_data('customer', this.index , this.value).then( (val) =>  {
                    this.usersList = val;
                    this.has = this.usersList.length == 0 ? false : true;
                });
            });*/
            //debugger;
            Page.query('customer').then( (response) => {
                this.usersList = response.data;
                this.goNext = response.next;
                this.has = this.usersList.length == 0 ? false : true;
            });
        }
    }
</script>

<style scoped>
    .IndexedDB {
        padding-top: 1px;
        padding-bottom: 8px;
    }

	.user-edit-dialog {
	    position: fixed;
        top: 0px;
        z-index: 102;
	    width: 100%;
	    min-height: 100%;
	    height: auto;
	    background-color: rgba(0, 0, 0, .5);
	}

	.user-edit-content {
	    position: absolute;
        top: 50px;
        bottom: 100px;
        margin: 0px 4%;
        width: 92%;
        min-height: 420px;
	    border-radius: 8px;
	    overflow: scroll;
	    background-color: #fff;
	}

	.icon-close {
        position: absolute;
        bottom: 10px;
        right: 50%;
        margin-right: -20px;
        font-size: 40px;
        z-index: 102;
        color: rgba(0, 0, 0, .5);
	}

	.icon-del{
	    position: absolute;
        right: 28px;
        top: 5px;
        height: 20px;
        width: 20px;
	}

    @media screen and (max-height: 500px) { 
        .icon-close {
            top: 500px;
        }
    }
</style>



