<template>
	<div class="card">
		<div class="menu-page-item">
			<menu-list :index="index"></menu-list>
		</div>
	</div>
</template>

<script>
	import menuList from '../../common/menuList';	//菜单组件
	export default {
		name: 'card',
		components: {
			menuList
		},
		data() {
			return {
				index: "1-4-0"	//菜单层级
			}
		}
	}
</script>


