<template>
	<div class="menuList">
		<div class="menu-container">
			<template class="test" v-for="menu in menuList">
				<div class="menu-cell" @click="setPage(menu)">
					<i :class="menu.cl" class="iconfont public"></i>
					<span class="menu-name">{{menu.name}}</span>
				</div>
			</template>
		</div>
	</div>
</template>
<script>
	import JsonData from './json'
	export default {
		name: 'menuList',
		props: {
			index: [String]
		},
		computed: {
			'menuList': function() {
				let self = this;
				return JsonData.menu.filter( (item) => {
					return item.index == self.index;
				});
			}
		},
		methods: {
			setPage: function(menu) {
				this.$router.push('/' + menu.hash); //此处不使用<router-link>
				this.$bus.emit('child-page', true);
				this.$bus.emit('select-title', menu.name);
			}
		}
	}
</script>
<style scoped>
	.menuList {
		position: absolute;
		top: 40px;
		margin: 0px;
		padding: 0px;
		width: 100%;
		min-height: 100%;
		height: auto;
		background: url(../../assets/img/bg.jpg) no-repeat;
		background-size: 100% 100%;
		-webkit-background-size: 100% 100%;
	}
	
	.menu-container {
		margin: 20px 5px;
	}
	
	.menu-cell {
		float: left;
		padding: 0;
		margin: 0;
		height: 100px;
		text-align: center;
		color: #607D8B;
		font-weight: bold;
		width: 21%;
		margin: 2%;
	}
	
	.public {
		display: block;
		margin-bottom: 12px;
		font-size: 40px;
		color: #00BCD4;
	}
	
	.menu-name {
		padding: 6px;
		font-size: 13px;
		color: #fff;
		/*background-color: #26A2FF;*/
		border-radius: 4px;
	}
</style>


