<template>
    <div class="fieldContent">
        <mt-field label="姓名" placeholder="请输入姓名" v-model="addName"></mt-field>
        <mt-field label="手机" placeholder="请输入手机号" type="tel" v-model="addCall"></mt-field>
        <mt-field label="卡号" placeholder="请输入卡号" type="tel" v-model="addCard"></mt-field>
        <mt-field label="余额" placeholder="请输入余额" type="number" v-model="addMoney"></mt-field>
        <mt-field label="日期" placeholder="请输入日期" type="date" v-model="addDate"></mt-field>
        <mt-field placeholder="请输入备注信息" type="textarea" rows="5" v-model="addInfo"></mt-field>
        <mt-button class="btn" type="primary" @click="doSubmit">提交</mt-button>
    </div>
</template>

<script>
	import Vue from 'vue';
	import {Check} from '../../assets/js/validation'; //验证模块
	import DB from '../../assets/js/IndexedDB'; //存入IndexedDB
	import {
		Field,
		Indicator,
		Toast,
		MessageBox
	} from 'mint-ui';
	Vue.component(Field.name, Field);

	export default {
		name: 'fieldContent',
		data() {
			return {
				addName: '',
				addMoney: '',
				addCard: '',
				addMoney: '',
				addCall: '',
				addDate: '',
				addInfo: ''
			}
		},
		methods: {
			doSubmit: function() {
				let self = this;
				let array = [this.addName, this.addMoney, this.addCard, this.addCall, this.addDate, this.addInfo];
				Check.nullCheckArrayDo(array, this.doFaildFun, function() {
					if(!Check.callCheck(self.addCall)) {
						Toast({
							message: '请输入一个正常点的手机号',
							position: 'top',
							iconClass: 'iconfont icon-warn',
							duration: 1500
						});
						return;
					}
					self.doSubmitFun();
				});
			},
			doFaildFun: function() {
				Toast({
					message: '请认真填写完信息哦！',
					position: 'top',
					iconClass: 'icon iconfont icon-warn',
					duration: 1500
				});
			},
			doSubmitFun: function() {
				let obj = {
					name: this.addName,
					money: this.addMoney,
					card: this.addCard,
					call: this.addCall,
					date: this.addDate,
					info: this.addInfo
				}
				MessageBox({
					title: '提示',
					message: '确定提交该信息?',
					showCancelButton: true
				}).then(action => {
					if(action === 'confirm') {
						Indicator.open({
							text: '存入中...',
							spinnerType: 'snake'
						});
					    this.doSave('customer', obj).then( (val) => {
                            this.doClose(val);
                        });
					}
				});
			},
			
			doSave(name, val) {
			    return new Promise(function(resolve, reject) {
                    DB.add_data(name, val);
			        resolve('数据存储成功');
                    reject('数据存储失败');
			    });
			},
			doClose(val) {
			    Indicator.close();
                let instance = Toast({
                    message: val,
                    duration: 500,
                    iconClass: 'mintui mintui-success'
                });
                this.addName = this.addMoney = this.addCard = this.addCall = this.addDate = this.addInfo = '';
                this.$bus.emit('edit-hide', false);  //隐藏编辑框
			}
		}
	}
</script>


