window.onload = function() {
  var Hash =  window.location.href.substring(window.location.href.indexOf('#') + 2);
  var flag = 1;
  window.addEventListener("popstate", function(e) {
    var doHash =  window.location.href.substring(window.location.href.indexOf('#') + 2);
    let arr = Hash.split('/');
    let len = arr.length;
    let str = '';
    for(let i = 0; i < (len-1); i++) {
      str += '/';
      str += arr[i];
    }
    str = str.substring(1);
    if(str == doHash) {
      if(Hash == 'home') {
        prompt("再点击一次退出该页面");
        flag++;
        var state = {
          title: "title",
          url: "#"
        };
        doHash == 'home';
        //if(flag == 2) {
        window.history.pushState(state, "title", "#/home");
        //}
        /*else {
          flag = 1;
          window.close();
        }*/
      }
    }
    Hash = doHash;
  }, false);

  /*function pushHistory() {
    var state = {
      title: "title",
      url: "#"
    };
    //window.history.pushState(state, "title", "#/index");
    window.location.navigate = 'index.html';
  }*/
};


