<template>
	<div class="navBar">
		<mt-header fixed :title="selectTitle">
			<i class="iconfont icon-fanhui icon-nav" @click="back" v-if="child" slot="left"></i>
			<i class="iconfont icon-search icon-nav" @click="search" slot="right"></i>
		</mt-header>
	</div>
</template>

<script>
	import Vue from 'vue';
	import {
		Header
	} from 'mint-ui';

	Vue.component(Header.name, Header);
	export default {
		name: 'navBar',
		props: {
			title: String,
			children: Boolean,
			titleMap: Object
		},
		data() {
			return {
				selectTitle: this.title,
				value: '',
				child: this.children //判断是否显示子页面返回图标
			}
		},
		methods: {
			changeTitle: function(val) {
				this.selectTitle = val;
			},
			search: function() {
				this.$bus.emit('search', true);
			},
			toChild: function(val) {
				this.child = val;
			},
			back: function() {
				/*
				 * 这里不能简单采用this.$router.go(-1)通过历史url跳转
				 * 1，全部的跳转用了replace而不是push；
				 * 2，避免用户是通过其他方式（如搜索）直接进入的子页面
				 * 获取跳转前的hash值，截取上一级页面的hash，作为跳转hash值
				 */
				let hash = window.location.href.substring(window.location.href.indexOf('#') + 2); //获取当前hash，判断是哪个tab主页面			//截取上一级hash	 
				let arr = hash.split('/');
				let len = arr.length;
				let str = '';
				for(let i = 0; i < (len-1); i++) {
					str += '/';
					str += arr[i];
				}
				str = str.substring(1);
				//若匹配不到合适的路由，就跳转到首页（意外情况）
				if(this.titleMap[str] == undefined) {
					str = 'home';
					len = 2;
				}
				//正常情况
				this.$router.replace('/' + str);
				if(len == 2) {
					//此时返回主页面
					this.child = false;
				}
				this.selectTitle = this.titleMap[str];
			},
			onSortVal(val) {
			    this.value = val;
			}
		},
		created() {
			this.$bus.on('select-title', this.changeTitle);
			this.$bus.on('child-page', this.toChild);
			this.$bus.on('to-parent', this.toChild);
		}
	}
</script>

<style scoped>
	.navBar {
		height: 8%!important;
		z-index: 99;
	}
	
	.icon-nav {
		font-size: 30px;
	}

</style>


