<template>
    <div class="moreDialog">
        <div class="background" @click="hideMe"></div>
        <div class="colorbox" >
            <div class="arrow-outer">
                <div class="arrow"></div>
            </div>
            <template v-for="value in list">
                <mt-cell :title="value" @click.native="toDo(value)" is-link>
                </mt-cell>
            </template>
            </mt-cell>
        </div>
    </div>
</template>
<script>

    export default{
        name: 'moreDialog',
        props: {
            list: Array
        },
        data(){
            return{

            }
        },
        methods: {
            //隐藏弹出层
            hideMe() {
                this.$bus.emit('hide-doMore', null);
            },
            //点击某一项具体操作
            toDo(val) {
                this.$bus.emit('todo-some', val);
            }
        }
    }
</script>
<style>
    .background {
        position: fixed;
        top: 0px;
        left: 0px;
        width: 100%;
        min-height: 100%;
        height: auto;
        z-index: 102;
        background: rgba(0, 0, 0, .2);
    }
    .colorbox {
        position: absolute;
        top: 0px;
        right: 0px;
        z-index: 103;
        width: 100%;
        height: auto;
        /*box-shadow: rgb(223, 223, 223) 1px 3px 3px;*/
        margin-top: 30px;
        border-width: 1px;
        border-style: solid;
        border-color: rgb(175, 175, 175);
        border-image: initial;
        border-radius: 3px;
        background-color: #fff;
    }

    .arrow-outer {
        z-index: 102;
        width: 12px;
        height: 8px;
        position: absolute;
        top: -7px;
        right: 32px;
        overflow: hidden;
    }

    .arrow {
        border: 1px solid #AFAFAF;
        width: 10px;
        height: 10px;
        position: relative;
        top: 2px;
        -webkit-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        background: #FFFFFF;
        -moz-transform: rotate(45deg);
    }
</style>



