(function () {
    //window.indexedDB = window.indexedDB || window.mozIndexedDB || 	window.webkitIndexedDB || window.msIndexedDB;
    if (!window.indexedDB && !window.mozIndexedDB && !window.webkitIndexedDB && !window.msIndexedDB) {
        console.log("你的浏览器不支持indexedDB");
    }
})(window);

let db;
const _db = {
    //db: null,
    flag: false,

    /*
     * 打开数据库
     * @name:数据库名称；@version：数据库版本号；@error：数据库错误回调
     */
    open: function (name) {
        const version = '1';
        return new Promise((resolve, reject) => {
            //打开数据库
            let req = window.indexedDB.open(name, version);
            //打开失败
            req.onerror = function (e) {
                reject("打开数据库错误:" + e.target.errorCode);
            };
            //打开成功
            req.onsuccess = function (e) {
                db = this.result;
                //_db.db = this.result;
                /*
                 * 数据打开成功，异步过程，监听这个状态
                 */
                _db.flag = true;
                resolve('数据库打开成功');
            };

            /*
             * onupgradeneeded事件
             * 每次更新数据库会执行该操作
             * 新增字段，更改索引等
             */
            req.onupgradeneeded = function (e) {
                //创建objectStore
                var objectStore = e.target.result.createObjectStore(name, {keyPath: "card"});
                objectStore.createIndex("name", "name", {unique: false});
                objectStore.createIndex("call", "call", {unique: true});
                objectStore.createIndex("card", "card", {unique: true});
                objectStore.createIndex("money", "money", {unique: false});
                objectStore.createIndex("date", "day", {unique: false});
                objectStore.createIndex("info", "info", {unique: false});
                objectStore.createIndex("sign", "sign", {unique: false});
                objectStore.createIndex("moneyname", ["money","name"], {unique: false});
                objectStore.createIndex("moneycard", ["money","card"], {unique: false});
            };
        });

    },
    /*
     * 新增用户
     */
    add_data: function (name, val) {
        return new Promise((resolve, reject) => {
            var transaction = db.transaction(name, 'readwrite');
            var store = transaction.objectStore(name);
            let request = store.add(val);

            request.onsuccess = function (e) {
                resolve('信息添加成功！');
            };
            request.onerror = function (e) {
                reject("错误:卡号已存在或信息有误" + e.target.errorCode);
            };
        });
    },

    read_data: function (name) {				//读取数据库所有信息
        return new Promise((resolve, reject) => {
            let data = new Array();
            let transaction = db.transaction(name, "readwrite");
            let objectStore = transaction.objectStore(name);
            objectStore.openCursor().onsuccess = function (event) {
                let cursor = event.target.result;
                if (cursor) {
                    data.push(cursor.value);
                    cursor.continue();
                } else {
                    resolve(data);
                }
            }
        })

    },

    /*
     * 根据单字段匹配查询信息
     */
    query_data: function (name, type, val) {				//读取数据库所有信息
        return new Promise((resolve, reject) => {
            let data = new Array();
            //debugger;
            let transaction = db.transaction(name, "readwrite");
            let objectStore = transaction.objectStore(name);
            let req, range;
            /*
             * 避免参数传递过程出现异常
             */
            val = val == undefined ? '' : val;
            if(type == undefined) {
                range = IDBKeyRange.bound(val, val+'\uffff',false,false);
                req = objectStore.openCursor(range);
            }else {
                range = IDBKeyRange.bound(val, val+'\uffff',false,false);
                let index = objectStore.index(type);
                req = index.openCursor(range);
            }

            req.onsuccess = function(event) {
                let cursor = event.target.result;
                if (cursor) {
                    data.push(cursor.value);
                    cursor.continue();
                } else {
                    resolve(data);
                }
            };
            req.onerror = function(event)
            {
                reject("数据库查询失败:" + e.target.errorCode);
            }
        });
    },
    /*
     * 更新信息
     */
    update_data: function (name, val) {
        return new Promise((resolve, reject) => {
            var transaction = db.transaction(name, 'readwrite');
            var store = transaction.objectStore(name);
            let request = store.put(val);

            request.onsuccess = function (e) {
                resolve('信息添加成功！');
            };
            request.onerror = function (e) {
                reject("错误:卡号不能更改");
            };
        });
    },

    /*
     * 删除信息
     */
    del_data: function (name, key) {
        return new Promise((resolve, reject) => {
            var transaction = db.transaction(name, 'readwrite');
            var store = transaction.objectStore(name);
            let request = store.delete(key);

            request.onsuccess = function (e) {
                resolve('信息删除成功！');
            };
            request.onerror = function (e) {
                reject("错误:信息删除失败");
            };
        });
    }



};

export default _db;


