<template>
	<div class="lostFound">
		<div class="lost-found-outer">
			<i class="iconfont icon-404 lost-found-title"></i>
			<svg class="icon lost-and-found">
				<use xlink:href="#icon-yemian"></use>
			</svg>
		</div>
		<div class="lost-found-do">
			<span class="reload-page" @click="lostFoundDo(hashPath)">
				<i class="iconfont icon-home icon-size"></i>
				刷新页面
			</span>
			<span class="line">|</span>
			<span class="lost-back" @click="backHome">
				<i class="iconfont icon-home icon-size"></i>
				返回首页
			</span>
		</div>
	</div>
</template>
<script>
	export default {
		name: 'lostFound',
		data() {
			return {

			}
		},
		computed: {
			'hashPath': function() {
				let path = window.location.href.substring(window.location.href.indexOf('#') + 2); //获取url连接地址
				return path;
			}
		},
		methods: {
			lostFoundDo(path){
				window.location.reload();
				/*this.$router.push('/' + path);*/
			},
			backHome: function() {
				this.$bus.emit('to-parent', false);
				this.$router.replace('/home');
			}
		}
	}
</script>
<style scoped>
	.lostFound {
		margin: 40px auto 60px;
	}
	.lost-found-outer {
		margin: 20px 10%;
		width: 80%;
		text-align: center;
	}
	
	.lost-found-title {
		display: block;
		padding-top: 60px;
		font-size: 40px;
		color: #0caee7;
	}
	
	.lost-and-found {
		width: 260px;
	}
	.lost-found-do {
		text-align: center;
		color: #0caee7;
		margin-top: 30px;
	}
	.icon-size {
		font-size: 18px;
	} 
	.line {
		margin: 0 20px;
	}
</style>


