<template>
    <div class="transform">
        <div class="menu-page-item">
            <menu-list :index="index"></menu-list>
        </div>
    </div>
</template>

<script>
	import menuList from '../../common/menuList';
	export default {
		name: 'transform',
		components: {
			menuList
		},
		data() {
			return {
				index: '1-2-0'
			}
		}
	}
</script>


