<template>
	<div class="search">
		<i class="iconfont icon-fanhui icon-return" @click="backPage"></i>
		<mt-search v-model="value" class="mt-search" placeholder="快速搜索（如：账户 / 管理 / 卡）">
			<mt-cell v-for="item in filterResults" @click.native="fastInit(item)" :title="item.name" value=">">
			</mt-cell>
		</mt-search>
	</div>
</template>
<script>
	import {Check} from '../assets/js/validation.js';
	import JsonData from './common/json'
	export default {
		name: 'search',
		data() {
			return {
				value: ""
			}
		},
		computed: {
			/*
			 * vue 2.0 取消了内置的filterBy，可以使用此方法替代
			 */
			filterResults() {
				/*let val = this.value;
				return this.result.filter(function(item) {
					return item.name.toLowerCase().indexOf(val.toLowerCase()) !== -1;
				})*/
				return Check.filterResults(this.value, this.result, 'name');

			},
			title() {
				return JsonData.titleMap;
			},
			result() {
				return JsonData.menu;
			}
		},
		methods: {
			backPage() {
				this.$bus.emit('search', false);
			},
			fastInit(item) {
				this.$router.push('/' + item.hash);
				this.$bus.emit('search', false);
				/*
				 * 获取此时hsah值，并判断激活title
				 * 获取此时hsah值，并判断激活tab（获取hash值第一位，其他tab主页面会加子页面，因此加上判断）
				 */
				let val = item.hash.split('/')[0];
				this.$bus.emit('child-page', true);
				let tab = this.title[val];
				let title = item.name;
				this.$bus.emit('select-title', title); //抛出此时选择的导航标题
				this.$bus.emit('select-tab', tab);
			}
		}
	}
</script>
<style>
	.search {
		position: fixed;
		z-index: 101;
		width: 100%;
		min-height: 100%;
		height: auto;
	}
	
	.mt-search {
		position: absolute;
		top: -35px;
		left: 12%;
		width: 85%;
	}
	
	.icon-return {
		position: absolute;
		top: -35px;
		left: 5px;
		font-size: 30px!important;
		color: #26a2ff;
	}
</style>


