<template>
	<div class="offen contents">
		<mt-radio
		  title="请选择"
		  v-model="radioValue"
		  :options="options">
		</mt-radio>
		<mt-checklist
		  title="自定义列表"
		  v-if="showDesign"
		  :max="2"
		  v-model="checkValues"
		  :options="menuOptions">
		</mt-checklist>
		<mt-button class="btn" v-if="showDesign" type="primary" @click="submit">确定</mt-button>
	</div>
</template>
<script>
	import Vue from 'vue'
	import
		{ 
		Checklist,
		Radio
		} from 'mint-ui';

	Vue.component(Checklist.name, Checklist);
	Vue.component(Radio.name, Radio);
	export default {
		name: 'offen',
		data() {
			return {
				radioValue: 'offen',
				checkValues: ['home/account/add', 'home/account/query'],
				design: false,
				options: [
				{
					label: '根据最近使用定制',
				    value: 'offen'
				},
				{
				    label: '自定义',
				    value: 'design'
				}],
				menuOptions: [
				{
					label: '存入账户',
				    value: 'home/account/add'
				},{
				    label: '查询账户',
				    value: 'home/account/query'
				},{
				    label: '加挂账户',
				    value: 'home/account/collection'
				}]
			}
		},
		computed: {
			showDesign: function() {
				return this.radioValue == 'design' ? true : false;
			}
		},
		methods: {
			submit() {
				console.log(this.checkValues);
				this.$bus.emit('set-tab', this.checkValues);
			}
		}
	}
</script>


