<template>
	<div class="accountCollection">
		 <template v-if="has">
            <div class="user-content" v-for="value in usersList">
                <!--    操作该条信息  -->
                <!-- <img src='../../../assets/img/more.png' class="icon-del" @click.stop="doMore(value)"/>
                <domore-dialog :list="doMoreList" v-if="more == value.call"></domore-dialog> -->
                <!--    操作该条信息  -->
               
                <!--    查询结果：用户列表   -->
                <ul>
                    <li class="user-list" v-for="(value, key) in value">{{labelMap[key]}}：{{value}}</li>
                </ul>
            </div>
           
        </template>
        <!--    没有信息显示内容    -->
        <template v-if="!has">
            <p class="no-data-title">啊哦！你的数据被偷走了，立刻去存点数据吧</p>
            <i class="iconfont icon-shibai no-data"></i>
        </template>
	</div>
</template>

<script type="es6">
	import DB from '../../../assets/js/IndexedDB'       //引入IndexedDB，数据库操作文件
	export default {
		name: 'accountCollection',
		data() {
			return {
				has: false,
				usersList: null,
				 //根据index，回显汉字title
                labelMap: {
					name: '姓名',
                    call: '手机',
					card: '卡号',
                    money: '余额',
					date: '日期',
					info: '备注',
					sign: '标注用户'
				}
			}
		},
		methods: {
			doQuery() {

			}
		},
		created() {
			//打开数据库并查询信息
            DB.open('customer').then( (resolve) => {
                DB.query_data('customer', 'sign' , '加挂帐户').then( (val) => {
                    this.usersList = val;
                    this.has = this.usersList.length == 0 ? false : true;
                });
            });
		}
	}
</script>

<style type="text/css">
	.accountCollection {
		padding-top: 3px;
		padding-bottom: 20px;
	}
</style>


