<template>
    <div>
        <mt-field label="姓名" placeholder="请输入姓名" :attr="{ maxlength: 16 }" v-model="name"></mt-field>
        <mt-field label="手机" placeholder="请输入手机号" type="tel" v-model="call"></mt-field>
        <mt-field label="卡号" placeholder="请输入卡号" type="tel" :attr="{ maxlength: 19, minlength: 16 }" disabled v-model="card"></mt-field>
        <mt-field label="金额" placeholder="请输入金额" type="number" v-model="money"></mt-field>
        <mt-field label="日期" placeholder="请输入日期" type="date" v-model="date"></mt-field>
        <mt-field placeholder="请输入备注信息" type="textarea" rows="3" v-model="info"></mt-field>
        <mt-button class="btn" type="primary" @click="doSubmit">修改</mt-button>
    </div>
</template>
<script>
    import Vue from 'vue'
    import DB from '../../assets/js/IndexedDB'
    import {
		Field,
		Indicator,
		Toast,
		MessageBox
	} from 'mint-ui';
	Vue.component(Field.name, Field);
    export default{
        name: '',
        props: {
            uesrInfo: [Object, Array]
        },
        data(){
            return{
                infomation: this.uesrInfo,
                name: '',
                money: '',
                call: '',
                card: '',
                date: '',
                info: ''
            }
        },
        methods: {
            doSubmit() {
                let self = this;
                let obj = {
					name: this.name,
					money: this.money,
					card: this.card,
					call: this.call,
					date: this.date,
					info: this.info
				};
                MessageBox({
					title: '提示',
					message: '确定修改信息?',
					showCancelButton: true
				}).then( action => {
					if(action == 'confirm') {
					    self.doUpdate('customer', obj).then( (val) => {
                            self.doClose(val);
					    });
					}
				});
            },
            doUpdate(name, obj) {
                return new Promise(function(resolve, reject) {
                    DB.update_data(name, obj).then( (val) => {
                        resolve(val);
                    }, function(reject) {
                        alert(reject);
                    });
                });
            },
            doClose(val) {
                this.$bus.emit('hide-edit-dialog', false);   //隐藏编辑信息弹出层
                let instance = Toast({
                    message: val,
                    duration: 500,
                    iconClass: 'mintui mintui-success'
                });
                this.name = this.age = this.call = this.card = this.date = this.info = '';
            }


        },
        created() {
            let {name, money, call, card, date, info} = this.infomation;
            this.name = name;
            this.money = money;
            this.call = call;
            this.card = card;
            this.date = date;
            this.info = info;
        }
    }

</script>



