import DB from './IndexedDB';
let limit = 4;
let len;
let p;
const _page = {
	query(name) {
		return new Promise((resolve, reject) => {
			DB.open(name).then( () => {
				DB.read_data(name).then( (val) => {
					//debugger;
					len = val.length;
					p = Math.ceil(len/limit);
					let next;
					if(p > 1) {
						next = true;
						let data = val.slice(0,limit);
						resolve({data, next});
					}else {
						next = false;
						let data = val;
						resolve({data, next});
					}
					
				})
			});
		});
	},
	last(name, page) {
		return new Promise((resolve, reject) => {
			DB.read_data(name).then( (val) => {
				page = Number(page);
				let start = (page-2)*limit;
				let end = (page-1)*limit;
				let last = true;
				if(page == '2') {
					last = false;
				}
				let data = val.slice(start, end);
				resolve({data, last});
			});
		});
	},
	next(name, page) {
		return new Promise((resolve, reject) => {
			DB.read_data(name).then( (val) => {
				page = Number(page);
				//debugger;
				let start = page*limit;
				let end = (page+1)*limit;
				let next = true;
				if(page == (p-1)) {
					next = false;
				}
				let data = val.slice(start, end);
				resolve({data, next});
			});
		});
	}

}
export default _page;


