<template>
	<div class="history">
		<div class="history-title">本次新增</div>
		<template v-for="item in historyList">
			<div class="history-content">
				<mt-cell :title="item.card" is-link>
	            	<span>{{item.name}}</span>
	        	</mt-cell>
			</div>
		</template>
		
	</div>
</template>

<script type="es6">
	import Storage from '../../assets/js/localStorage'; //存入localStorage
	export default {
		name: 'history',
		computed: {
			historyList() {
				let data = Storage.getSession('user-info');
				if(data == null || data == undefined) {
					data = '[]'; //不存在就返回空数组形式字符串
				}
				return JSON.parse(data);
			}
		}
	}
</script>

<style type="text/css" scoped>
	.history-title {
	    text-align: center;
	    font-size: 16px;
	    margin: 1px auto;
	    padding-top: 20px;
	    padding-bottom: 20px;
	    background: #fff;
	}

	.history-content {
		background-color: #fff;
	}
</style>


