<template>
	<div class="signIn">
		<template v-if="signIn">
			<div class="login-container">
				<div class="input-group">
					<mt-field placeholder="请输入名称" v-model="signInName" @keyup.native.13="doSubmit"></mt-field>
					<mt-field placeholder="请输入密码" type="password" v-model="signInPassword" @keyup.native.13="doSubmit"></mt-field>
				</div>

				<mt-button class="login-btn" type="primary" size="large" @click.native="doSubmit">
					<i class="iconfont icon-loginOut"></i> 登录
				</mt-button>

				<div class="login-footer">
					<span class="lost-pass">忘记密码</span><span class="line">|</span><span class="to-signUp" @click="signIn = false">注册账号</span>
				</div>
			</div>
		</template>

		<template v-if="!signIn">
			<div class="login-container">
				<div class="input-group">
				 	<mt-field placeholder="请输入注册名称" v-model="signUpName" autofocus></mt-field>
					<mt-field placeholder="请输入注册密码" type="password" v-model="signUpPassword"></mt-field>
					<mt-field placeholder="请确认注册密码" type="password" v-model="ResignUpPassword"></mt-field>
				</div>

				<mt-button class="login-btn" type="primary" size="large" @click.native="doSubmit">
					<i class="iconfont icon-loginOut"></i> 注册
				</mt-button>

				<div class="login-footer">
					<div class="to-signIn" @click="signIn = true">已有账号，点我登录</span>
				</div>
			</div>
		</template>
	</div>
</template>

<script type="es6">
	import Vue from 'vue';
	import {Check} from '../../assets/js/validation'; //验证模块
	import {
		Toast
	} from 'mint-ui';
	export default {
		name: 'signIn',
		data() {
			return {
				signIn: true,
				signInName: '',
				signInPassword: '',
				signUpName: '',
				signUpPassword: '',
				ResignUpPassword: ''
			}
		},
		methods: {
			doSubmit() {
				let self = this;
				let array = self.signIn ? [this.signInName, this.signInPassword] : [this.signUpName, this.signUpPassword,  this.ResignUpPassword];
				Check.nullCheckArrayDo(array, this.doFaildFun, function() {
					if(self.signIn) {
						self.doSignInFun();
					}
					else {
						if(self.signUpPassword !== self.ResignUpPassword) {
							Toast({
								message: '两次密码输入不一致',
								position: 'top'
							});
							return;
						}
						self.doSignUpFun();
					}
					
				});
				
			},
			doFaildFun: function() {
				Toast({
					message: '请认真填写完信息哦！',
					position: 'top',
					iconClass: 'icon iconfont icon-warn',
					duration: 1500
				});
			},
			doSignInFun() {
				if(this.signInName === "admin" && this.signInPassword === "admin") {
					let time = new Date().getTime();
					sessionStorage.setItem('loginStaus', time);
					this.$bus.emit('hide-sign', false);
				} else {
					Toast({
						message: '账号不存在或密码错误',
						position: 'top'
					});
				}
			},
			doSignUpFun() {
				alert('请用账户admin，密码admin登录');
			}
		}

	}
</script>

<style type="text/css">
	.signIn {
		position: absolute;
		top: 0px;
		left: 0px;
		width: 100%;
		min-height: 100%;
		height: auto;
		background: url(../../assets/img/bg2.jpg);
		background-size: 100% 100%;
	}
	.login-container {
		position: absolute;
		top: 80px;
		margin: 0px 5%;
		width: 90%;
	}

	.input-group {
		background-color: rgba(255,255,255,.6);

	}

	.login-btn {
		margin: 30px auto;
		opacity: .6;
	}

	.login-footer {
		text-align: center;
		margin-top: 66px;
		color: #2196F3;
	}

	.to-signUp, .to-signIn, .lost-pass {
		cursor: pointer;
	}
	
	.line {
		margin: 0 10px;
	}
</style>


